

:root {
  
  --text-color: #fff;
  --main-color:  #16c0f0;
  --border-color: #bdc3;
  --background-color: #2b3d4f;
  
  --transition: all 400ms ease;
}
body {
    font-size: 1rem;
    font-weight: 400;
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 1.5;
    color: #374649;
    text-align: left;
    background-color: #f8f9fa;
    width: 100%;
    height: auto;
  
  }
  body, html {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-x: hidden; 
  }

  .loader{
    background: var(--background-color);
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  
  .loader-spanne-20 {
    position: relative;
    width: 100px;
    height: 30px;
    padding: 0;
  }
  .loader-spanne-20 > span {
    position: absolute;
    right: 0;
    width: 3px;
    height: 30px;
    background-color: rgb(116, 204, 197);
    display: block;
    border-radius: 3px;
    transform-origin: 50% 100%;
    animation: move 2.8s linear infinite;
  }
  .loader-spanne-20 > span:nth-child(1) {
    animation-delay: -0.4s;
  }
  .loader-spanne-20 > span:nth-child(2) {
    animation-delay: -0.8s;
  }
  .loader-spanne-20 > span:nth-child(3) {
    animation-delay: -1.2s;
  }
  .loader-spanne-20 > span:nth-child(4) {
    animation-delay: -1.6s;
  }
  .loader-spanne-20 > span:nth-child(5) {
    animation-delay: -2s;
  }
  .loader-spanne-20 > span:nth-child(6) {
    animation-delay: -2.4s;
  }
  .loader-spanne-20 > span:nth-child(7) {
    animation-delay: -2.8s;
  }
  @keyframes move {
    0% {
      opacity: 0;
      transform: translateX(0px) rotate(0deg);
    }
    20% {
      opacity: 1;
    }
    40% {
      transform: translateX(-40px) rotate(0deg);
    }
    50% {
      opacity: 1;
      transform: translateX(-50px) rotate(22deg);
    }
    85% {
      opacity: 1;
      transform: translateX(-85px) rotate(60deg);
    }
    100% {
      opacity: 0;
      transform: translateX(-100px) rotate(65deg);
    }
  }
  